
// eslint-disable-next-line @typescript-eslint/ban-ts-comment
// @ts-nocheck
import AppButton from "@/utils/AppButton.vue";
import Vue from "vue";
import { mapMutations } from "vuex";
export default Vue.extend({
  components: { AppButton },
  data: () => ({
    password: "",
    repassword: "",
    newUsername: "",
    name: "",
  }),
  computed: {
    isSamePassword(): boolean {
      return Boolean(this.password && this.password === this.repassword);
    },
    isValidUsername(): boolean {
      return Boolean(
        this.newUsername &&
          this.newUsername.length >= 4 &&
          !this.newUsername.includes(" ")
      );
    },
    isValidName(): boolean {
      return Boolean(this.name);
    },
  },
  methods: {
    ...mapMutations("snackbar", ["showSnackbar"]),
    async changePassword() {
      try {
        await this.axios.put("/user/password", {
          username: this.$cookies.get("username"),
          new_password: this.password,
        });

        this.password = "";
        this.repassword = "";

        this.showSnackbar({
          msg: "Berhasil Ubah Password",
          color: "success",
        });
      } catch (error) {
        console.log(error);
      }
    },
    async changeUsername() {
      try {
        await this.axios.put("user/username", {
          username: this.$cookies.get("username"),
          new_username: this.newUsername,
        });

        this.newUsername = "";
        this.showSnackbar({
          msg: "Berhasil Ubah Username",
          color: "success",
        });

        this.$cookies.remove("username");
        this.$cookies.remove("session_id");
        this.$cookies.remove("isAdmin");

        this.$router.go(0);
      } catch (error) {
        console.log(error);
      }
    },
    async changeName() {
      try {
        await this.axios.put("user/name", {
          username: this.$cookies.get("username"),
          new_name: this.name,
        });

        this.name = "";
        this.showSnackbar({
          msg: "Berhasil Ganti Nama",
          color: "success",
        });

        this.$router.go(0);
      } catch (error) {
        console.log(error);
      }
    },
    async logout() {
      try {
        await this.axios.post("user/logout", {
          username: this.$cookies.get("username"),
        });

        this.$cookies.remove("username");
        this.$cookies.remove("session_id");
        this.$cookies.remove("isAdmin");

        this.$router.go(0);
      } catch (error) {
        console.log(error);
      }
    },
  },
});
